import React, { Component } from 'react';
import {
  HeaderWrapper,
  HeaderSection,
  ButtonMenuWrapper
} from './Header.styles';
import { ReactComponent as Logo } from '../../../assets/logo.svg';
import { ReactComponent as Menu } from '../../../assets/menu.svg';
import { ReactComponent as Storage } from '../../../assets/storage.svg';
import { ReactComponent as Help } from '../../../assets/help.svg';
import { ReactComponent as Email } from '../../../assets/email.svg';
import { ReactComponent as Notification } from '../../../assets/notification.svg';
import Link from '../../../components/Link';
import Button from '../../../components/Button';
import { ButtonSkins, DropdownSkins } from '../../../types';
import Progress from '../../../components/Progress';
import Container from '../../../components/Container';
import Dropdown from '../../../components/Dropdown';
import { rootStore } from '../../../stores/RootStore';

interface IHeaderProps {}

class Header extends Component<IHeaderProps> {
  public render() {
    return (
      <HeaderWrapper>
        <HeaderSection>
          <Link to="?" style={{ position: 'relative', top: -8 }}>
            <Logo />
          </Link>
          <ButtonMenuWrapper>
            <Button
              skin={ButtonSkins.ICON}
              onClick={rootStore.dashboardStore.toggleNav}
            >
              <Menu />
            </Button>
          </ButtonMenuWrapper>
        </HeaderSection>
        <HeaderSection>
          <Progress percent={60} icon={Storage} />
          <Container marginRight={56} />
          <Dropdown skin={DropdownSkins.FLAT} renderBody={() => 'dsdad'}>
            <Help />
          </Dropdown>
          <Dropdown skin={DropdownSkins.FLAT} renderBody={() => 'asdasdd'}>
            <Email />
          </Dropdown>
          <Dropdown
            skin={DropdownSkins.FLAT}
            bodyWidth={272}
            renderBody={() => 'asdsadad'}
          >
            <Notification />
          </Dropdown>
          <Dropdown
            renderBody={() => (
              <Button
                onClick={rootStore.authStore.logout}
                skin={ButtonSkins.TRANSPARENT}
                width="100%"
              >
                Sign Out
              </Button>
            )}
            headerWidth={140}
          >
            {rootStore.authStore.name}
          </Dropdown>
        </HeaderSection>
      </HeaderWrapper>
    );
  }
}

export default Header;
